import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Plot from 'react-plotly.js';
import { useHistory } from 'react-router-dom';


const StockAnalysis = () => {
  const [tickers, setTickers] = useState([]);
  const [selectedTicker, setSelectedTicker] = useState('');
  const [stockData, setStockData] = useState(null);
  const [forecastData, setForecastData] = useState([]);

  const history = useHistory();

  useEffect(() => {
    axios.get('http://localhost:5000/api/stock-tickers')
      .then(response => {
        setTickers(response.data);
      })
      .catch(error => {
        console.error('Error fetching tickers:', error);
      });
  }, []);

  const handleTickerChange = (e) => {
    const ticker = e.target.value;
    setSelectedTicker(ticker);
    axios.get(`http://localhost:5000/api/stock-data/${ticker}`)
      .then(response => {
        setStockData(response.data);
      })
      .catch(error => {
        console.error('Error fetching stock data:', error);
      });
    axios.get(`http://localhost:5000/api/stock-forecast/${ticker}`)
      .then(response => {
        if (Array.isArray(response.data)) {
          setForecastData(response.data);
        } else {
          console.error('Unexpected response format for forecast data:', response.data);
          setForecastData([]);
        }
      })
      .catch(error => {
        console.error('Error fetching forecast data:', error);
      });
  };

  return (
    <div>
      <button onClick={() => history.goBack()}>Back</button>
      <h1>Stock Analysis</h1>
      <select value={selectedTicker} onChange={handleTickerChange}>
        <option value="" disabled>Select a stock ticker</option>
        {tickers.map(ticker => (
          <option key={ticker.stock_symbol} value={ticker.stock_symbol}>{ticker.company_name} ({ticker.stock_symbol})</option>
        ))}
      </select>
      {stockData && (
        <Plot
          data={[
            {
              x: stockData.map(d => d.date),
              y: stockData.map(d => d.close_price),
              type: 'scatter',
              mode: 'lines+markers',
              marker: { color: 'blue' },
              name: 'Historical Prices'
            }
          ]}
          layout={{ title: `Stock Prices for ${selectedTicker}` }}
        />
      )}
      {forecastData.length > 0 && (
        <Plot
          data={[
            {
              x: forecastData.map(d => d.forecast_date),
              y: forecastData.map(d => d.forecast_price),
              type: 'scatter',
              mode: 'lines+markers',
              marker: { color: 'red' },
              name: 'Forecasted Prices'
            }
          ]}
          layout={{ title: `Forecasted Prices for ${selectedTicker}` }}
        />
      )}
    </div>
  );
};

export default StockAnalysis;
